// store/task.ts
"use client";
import { create } from "zustand";
import { useAuthStore } from "./auth";
import axios from "axios";

export type Task = {
  _id: string;
  title: string;
  description: string;
  priority: "LOW" | "MEDIUM" | "HIGH";
  deadline: string;
};

type TaskState = {
  tasks: Task[];
  loading: boolean;
  error: string | null;
  fetchTasks: () => Promise<void>;
  createTask: (task: Omit<Task, "_id">) => Promise<void>;
  updateTask: (id: string, task: Partial<Task>) => Promise<void>;
  deleteTask: (id: string) => Promise<void>;
};

const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000";

const api = axios.create({
  baseURL: `${API_URL}/`,
  withCredentials: true,
});

export const useTaskStore = create<TaskState>((set, get) => ({
  tasks: [],
  loading: false,
  error: null,

  fetchTasks: async () => {
    set({ loading: true, error: null });
    try {
      const token = useAuthStore.getState().accessToken;
      const { data } = await api.get("/tasks", {
        headers: { Authorization: `Bearer ${token}` },
      });
      set({ tasks: data });
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        set({ error: err.response?.data?.message || "Error fetching tasks" });
      } else {
        set({ error: "Unexpected error" });
      }
    } finally {
      set({ loading: false });
    }
  },

  createTask: async (task) => {
    try {
      const token = useAuthStore.getState().accessToken;
      const { data } = await api.post("/tasks", task, {
        headers: { Authorization: `Bearer ${token}` },
      });
      set({ tasks: [...get().tasks, data] });
    } catch (err) {
      console.error(err);
    }
  },

  updateTask: async (id, task) => {
    try {
      const token = useAuthStore.getState().accessToken;
      const { data } = await api.put(`/tasks/${id}`, task, {
        headers: { Authorization: `Bearer ${token}` },
      });
      set({ tasks: get().tasks.map((t) => (t._id === id ? data : t)) });
    } catch (err) {
      console.error(err);
    }
  },

  deleteTask: async (id) => {
    try {
      const token = useAuthStore.getState().accessToken;
      await api.delete(`/tasks/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      set({ tasks: get().tasks.filter((t) => t._id !== id) });
    } catch (err) {
      console.error(err);
    }
  },
}));
